@import url(https://fonts.googleapis.com/css?family=Sanchez:400italic,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-color: #eeeeee;
  --clr-card: #ffffff;
  --clr-main: #2a78ff;
  --clr-font: #000000;
  --clr-secondary: 0.54;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 300;
  background-color: #eeeeee;
  background-color: var(--bg-color);
  min-height: 100vh;
}

.secondary {
  opacity: 0.54;
  opacity: var(--clr-secondary);
}

a {
  color: #000000;
  color: var(--clr-font);
  text-decoration: none;
  cursor: pointer;
}

.App .header.is-searching {
  height: 185px;
}

.App .header.is-searching #title {
  padding-top: 0.5em;
}

.App .header {
  background-color: #2a78ff;
  background-color: var(--clr-main);
  min-height: 185px;
  padding: 0 1.5em 0 1.5em;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  transition: height 0.5s;
}

.App .header .search-wrap {
  width: 100%;
  position: relative;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  max-width: 900px;
}

.App .header .input-search {
  padding-left: 1em;
  border: none;
  width: 100%;
  background-color: #ffffff;
  background-color: var(--clr-card);
  border-bottom: 1px solid #2a78ff;
  border-bottom: 1px solid var(--clr-main);
  color: #000000;
  color: var(--clr-font);
  height: 2.5em;
  transform: translateY(1.25em);
  border-radius: 3px 0 0 3px;
  outline: none;
}

.App .header .search-button {
  height: 2.5em;
  width: 3em;
  text-align: center;
  border: none;
  background-color: #ffffff;
  background-color: var(--clr-card);
  border-bottom: 1px solid #2a78ff;
  border-bottom: 1px solid var(--clr-main);
  border-radius: 0 3px 3px 0;
  cursor: pointer;
  transform: translateY(1.25em);
}

.App .header .search-button:hover {
  color: #2a78ff;
  color: var(--clr-main);
}

.App #title {
  margin: 0;
  color: #ffffff;
  color: var(--clr-card);
  font-size: 5em;
  font-weight: 900;
  text-align: center;
}

.App #title a {
  color: #ffffff;
  color: var(--clr-card);
}

.App #subtitle {
  margin: 0;
  color: #ffffff;
  color: var(--clr-card);
  font-weight: 300;
  text-align: center;
}

#main {
  padding: 1.5em;
}

#main .no-results {
  text-align: center;
}

#main .no-results::before {
  content: '\01F614';
}

#main a {
  display: block;
}

#main .movie-list {
  margin: 0.25em auto;
  max-width: 900px;
}

@media (min-width: 710px) {
  #main .movie-list {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
    grid-gap: 0 0.5em;
    gap: 0 0.5em;
  }
}

#main .movie-item {
  background-color: #ffffff;
  background-color: var(--clr-card);
  height: 6.75em;
  margin-bottom: 0.5em;
  border: 1px solid #ffffff;
  border: 1px solid var(--clr-card);
  border-radius: 5px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.06);
  display: flex;
  align-content: center;
}

#main .movie-item:hover {
  color: #2a78ff;
  color: var(--clr-main);
}

#main .movie-item > a {
  display: block;
  cursor: pointer;
}

#main .movie-item-image {
  object-fit: cover;
  height: 100%;
  width: auto;
  max-width: 4.5em;
  border-radius: 5px;
}

#main .movie-item-info {
  margin: 0.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#main .movie-item-title {
  margin: 0;
}

#main .movie-item-year {
  margin-top: 0.5em;
  font-size: 0.75em;
}

#main .movie-list-item-enter {
  opacity: 0;
  transform: translateY(50px);
}

#main .movie-list-item-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: all 300ms ease-in;
}

#main .movie-list-item-exit {
  opacity: 1;
  transform: translateY(0);
}

#main .movie-list-item-exit-active {
  opacity: 0;
  transform: translateY(50px);
  transition: all 300ms ease-in;
}

.movie-card {
  background-color: #ffffff;
  background-color: var(--clr-card);
  border: 1px solid #ffffff;
  border: 1px solid var(--clr-card);
  color: #000000;
  color: var(--clr-font);
  border-radius: 5px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.06);
  padding: 0 1em 1em 1em;
  display: flex;
  flex-direction: column;
  max-width: 900px;
  margin: 0 auto;
}

@media (min-width: 710px) {
  .movie-card {
    padding: 0;
    flex-direction: row;
    font-size: 1.2em;
  }
  .movie-card-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }
}

.movie-card-image {
  width: 65%;
  -ms-grid-row-align: center;
  align-self: center;
  border-radius: 0 0 5px 5px;
  max-width: 300px;
}

.movie-card-title {
  text-align: center;
  font-size: 1.25em;
  margin: 0.5rem 0 0.5rem 0;
}

@media (min-width: 710px) {
  .movie-card-title {
    font-size: 1.75em;
  }
}

.movie-card-director {
  text-align: center;
  margin: 0;
}

.movie-card-infos {
  text-align: center;
  font-size: 0.75em;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
}

.movie-card-infos-rated {
  text-align: right;
}

.movie-card-infos-year {
  text-align: left;
}

.movie-card-ratings {
  text-align: center;
  font-size: 0.75em;
  display: flex;
  justify-content: space-around;
}

.movie-card-ratings-item {
  flex: 1 1;
  display: flex;
  align-content: center;
  justify-content: center;
}

.movie-card-ratings-item > span {
  -ms-grid-row-align: center;
  align-self: center;
  padding-left: 0.25em;
}

blockquote {
  position: relative;
  font-style: italic;
  margin: 1em;
}

blockquote footer {
  font-size: 0.75em;
  font-style: normal;
  float: right;
  opacity: 0.5;
}

blockquote footer:before {
  content: '\2015';
}

blockquote footer:after {
  content: '\201D';
  position: absolute;
  font-family: 'Sanchez', serif;
  line-height: 2rem;
  right: 0.1rem;
  font-size: 6rem;
  opacity: 0.25;
}

.mc-good {
  color: #ffffff;
  padding: 0.25em;
  font-weight: 400;
  background-color: #6c3;
}

.mc-mixed {
  color: #ffffff;
  padding: 0.25em;
  font-weight: 400;
  background: #fc3;
}

.mc-bad {
  color: #ffffff;
  padding: 0.25em;
  font-weight: 400;
  background: #f00;
}

.spinner {
  margin: 0 auto;
  width: 60px;
  height: 40px;
  text-align: center;
  font-size: 16px;
}

.spinner > div {
  background-color: #2a78ff;
  background-color: var(--clr-main);
  height: 100%;
  width: 6px;
  display: inline-block;
  margin-right: 3px;
  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
}

.spinner .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.spinner .rect3 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.spinner .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

.spinner .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}

@-webkit-keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    -webkit-transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
  }
}

@keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }
  20% {
    transform: scaleY(1);
    -webkit-transform: scaleY(1);
  }
}

/*Simple css to style it like a toggle switch*/
.theme-switch-wrapper {
  position: fixed;
  top: 0.5em;
  right: 0.5em;
  display: flex;
  align-items: center;
  font-size: 2em;
}

.theme-switch {
  display: inline-block;
  height: 1em;
  position: relative;
  width: 2em;
}

.theme-switch input {
  display: none;
}

.slider {
  background-color: #ccc;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.4s;
}

.slider:before {
  background-color: #fff;
  bottom: 0.25em;
  content: '';
  height: 0.5em;
  left: 0.25em;
  position: absolute;
  transition: 0.4s;
  width: 0.5em;
}

input:checked + .slider {
  background-color: #66bb6a;
}

input:checked + .slider:before {
  transform: translateX(1em);
}

.slider.round {
  border-radius: 2.15em;
}

.slider.round:before {
  border-radius: 50%;
}

